@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
    font-family: 'Roboto', sans-serif;
}

html, body, #root, .containerLogin {
    height: 100%;
    width: 100%;
    display: flex;
    background-color: var(--primary-background-color);
    flex: 1 1;
}


.containerLogin {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#box-login {
    width: 500px;
    height: 50%;
    background-color: rgba(218, 217, 236, 0);
    border-radius: 10px;
    align-items: center;
    justify-content: center;
}
header {
    width: 100%;
    height: 45px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    border-radius: 5px 5px 0 0;
    color: rgb(6, 2, 66);
}
h2 {
    color: #fff;
    font-size: 25px;
    text-align: center;
    border-bottom: 1px solid #000;
}
main {
    width: 100%;
    height: 200px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
input {
    height: 40px;
    width: 90%;
    margin-top: 20px;
    border-radius: 0.3em;  
    border: 0.5px solid var(--inputs-border-color); 
}
#area-buttons {
    width: 100%;
    height: 70px;
    justify-content: space-between;
    align-items: center;
    display: flex;
}
button {
    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 15px;
    background-color: var(--button-background-color) ;
    color: #fff;
    cursor: pointer;
    margin-left: 24px;
}

a {
    text-decoration: none;
}

span {
    color: var(--text-sidebar-color);
    font-size: small;
}

@media only screen and (max-width: 599px){
    #box-login {
        width: 100%;
    }
   
}


* {
  padding: 0;
  margin: 0;
  box-sizing: 0;
  font-family: 'Roboto', sans-serif;
}

html, body, #root, #container  {
  height:100%;
  width: 100%;
  overflow-x: visible;

}

.blue {
  background: rgb(9, 87, 160);
}
.darkblue {
  background: var(--primary-background-color);
}

#container {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  overflow: auto;
  
}
.floors {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  padding-bottom: 13px;
  width: 100%;
  font-size: 20px;
  color: #fff;
  font-weight: 900;
  height: 45px;
  text-align: center;
  line-height: 45px;
}

.imageCostumer {
  width: 120px;
  height: 65px;
  margin-bottom: 5px;
  object-fit: scale-down;
}
.numberCostumer {
  color: rgb(9, 0, 78);
  font-weight: 600;
}

.containerRoom {
  display: flex;
  flex-direction: column;
  margin: 0px 0px 5px 5px;
}


@media only screen and (max-width: 599px){
  .floors {
    width: 411px;
  }
  .imageCostumer {
    width: 60%;
  }
  .numberRoom {
    width: 30%;
  }
  .containerWeather {
      display: none;
  }
}

.container-administration {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-start;
}

.container-sidebar {
    height: 100%;
    background-color: var(--sidebar-background-color);
    width: 290px;
    display: flex;
    flex-direction: column;
    border-right: 0.8px solid rgb(211, 211, 211);
}
.container-sidebar header {
    width: 100%;
    height: 90px;
    background-color: ivory;
    border-bottom: 0.9px solid rgb(236, 236, 236);
}
.container-sidebar header h2 {
    color: black;
    font-family: 'Roboto',sans-serif;
    font-size: 20px;
    text-align: center;
    width: 100%;
}
.container-sidebar nav {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container-sidebar nav ul li {
    width: 100%;
    height: 50px;
    display: flex;
    margin-top: 5px;
    margin-bottom: 5px;
    flex-direction: row;
    align-items: center;
    font-weight: 600;
    font-size: 17px;
    font-family: 'Roboto',sans-serif;
    color: var(--text-sidebar-color);
    cursor: pointer;

}
.container-sidebar nav ul li:hover {
    background-color: var(--hover-item-sidebar-color);
}
.container-sidebar nav ul li ul {
    width: 100px;
}
.container-sidebar nav ul li i {
    width: 40px;
    height: 100%;
    padding-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    text-align: center;
    color: var(--icons-sidebar-color);
    font-size: 25px;
}

.container-main {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
}

.container-navbar {
    height: 80px;
    width: 100%;
    background-color: --navbar-background-color;
    background-color: var(--primary-background-color,--navbar-background-color);
}
.container-navbar ul {
    width: 150px;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
}
.container-navbar ul li {
    list-style-type: none;
    cursor: pointer;
    height: 100%;
    padding-bottom: 10px;
    text-align: center;
    width: 30px;
    display: flex;
    flex-direction: column;
    flex-direction: column-reverse;
    
}
.container-navbar ul li:hover {
    border-bottom: 1px solid #fff;
}   
.container-navbar ul li i {
    font-size: 20px;
    color: var(--icons-navebar-color);
}

.main {
height: 100%;
width: 100%;
background-color: var(--main-background-color);
}

.primary-text-color{
    color: var(--primary-text-color) ;
    text-align: center;
}
.contianerCreateUser {
    flex: 1 1;
    margin: auto;
    width: 600px;
    height: 100%;
    display: flex;
    align-items:center ;
    flex-direction: column;
    padding-top: 40px;  
 }

.inputs {
    border: 0.1px solid .000;
    border-width: 1px;
    border-style: solid;
    border-color: var(--inputs-border-color);
    color: .000;
}

.primary-color {
    width: 90%;
    text-align: start;
    border: none;

}
.contianerCreateUser div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

#verify {
    color: rgb(18, 141, 6);
}
#incorret {
    color: rgb(136, 12, 12);
}
.options-admin {
    margin: 13px;
    width: 90%;
    height: 25px;
}

@media only screen and (max-width: 599px){
    .contianerCreateUser div button {
      margin: 0px;  
    }
}
.contianerAlterPassword {
    flex: 1 1;
    height: 100%; 
    width: 600px;
    padding-top: 40px;
    margin: auto;
    display: flex;
    align-items:center;
    flex-direction: column;
}

.contianerAlterPassword div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contianerListUsers{ 
    float: 1;
    height: 100%;
    display: flex;
    
}
h2 {
    border-bottom: none;
    margin-bottom: 10px ;

}
#list {
    width: 100%;
    height: 400px;
    overflow-y: auto;
}
.item-list {
    color: var(--primary-text-color);
    display: block;
    text-align: center;
    height: 30px;
    font-weight: bold;
    
}

#list table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }

#list td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  color: var(--primary-text-color);
  font-family: "Roboto",sans-serif;
}

#list tr:nth-child(even) {
    background-color: #8f8f8f;
}

@media only screen and (max-width: 599px){
    .contianerListUsers {
        width: 100%;
    } 
    .contianerListUsers #list {
        width: 100%;
    }
}

#contianerAddRoom {
    flex: 1 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    padding-left: 20px;
    margin: auto;
    width: 600px;
}

#contianerAddRoom h2 { 
    text-align: start;
}

input[type="file"]::-webkit-file-upload-button{
    visibility: hidden;
    opacity: 0;
}

input[type="file"]::before {
    content: 'Enviar imagem';
    display: inline-block;
    height: 80%;
    width: 120px;
    background: #e3e3e3;
    border-radius: 0.5em;
    padding: 5px 10px 0px 50px;
    z-index: 1;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    text-shadow: 1px 1px #fff;
    font-weight: 900;
    line-height: 30px;
    font-size: 10pt;
}

input[type="file"]{
    border: blue;
}

#contianerAddRoom div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.contianerUpdateRoom {
    flex: 1 1;
    height: 100%;
    width: 600px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 20px;
 

}
.contianerUpdateRoom h2 {
    text-align: center;
    margin-top: 40px;
}
.contianerUpdateRoom div {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 200px;
}
.contianerUpdateRoom div button {
    width: 90%;
}

.contianerDeleteRoom {
    flex: 1 1;
    height: 100%;
    width: 600px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    padding-left: 20px;
 }

.contianerDeleteRoom h2 { 
    text-align: center;
}

.contianerDeleteRoom div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: left;
    align-items: flex-end;
}

.contianerDeleteRoom div {
    display: flex;
    justify-content: center;
}


.contianerDeleteUser {
    flex: 1 1;
    height: 100%;
    width: 600px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    padding-left: 20px;
}

.contianerDeleteUser h2 { 
    text-align: start;
}

.contianerDeleteUser div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: left;
    align-items: flex-end;
}

.contianerDeleteUser div  {
    display: flex;
    justify-content: center;
}

.containerActionAlert {
    position: absolute;
    width: 200px;
    height: 50px;
    right: 5px;
    background: var(--actionalert-background-color);
    -webkit-animation: showalert 3s;
            animation: showalert 3s;
    border-radius: 0.3em;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.containerActionAlert p {
    color: aliceblue;
}

@-webkit-keyframes showalert {
    from {
        top: 0px;
        opacity: 0;
    }
    to {
        top: 50px;
        opacity: 1;

    }
}

@keyframes showalert {
    from {
        top: 0px;
        opacity: 0;
    }
    to {
        top: 50px;
        opacity: 1;

    }
}
:root{
    --primary-background-color: #076963;
    --actionalert-background-color: rgb(0, 87, 3);
    --button-background-color: #010b3d;
    --inputs-border-color: rgb(144, 144, 144);
    --sidebar-background-color: #fff;
    --primary-text-color: rgb(2, 1, 59);
    --main-background-color: rgb(251, 251, 251);
    --icons-sidebar-color: rgb(109, 109, 109) ;
    --icons-navebar-color:#fff;
    --hover-item-sidebar-color:rgba(221, 221, 221, 0.727);
    --text-sidebar-color: rgb(109, 109, 109);
    --navbar-background-color: rgb(2, 94, 80);
}


